body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-light .navbar-brand {
    color: white;
}

a:not([href]):not([tabindex]) {
    color: white;
}

a:not([href]):not([tabindex]):hover {
    color: white;
}

.nav-item {
    color: white;
}
@media only screen and (max-width: 767px) {
    .main-word {
        font-weight: bold;
        font-size: 10vw;
        position: static;
        letter-spacing: 10px;
        margin-top: 20px;
    }
}

@media only screen and (min-width: 768px) {
    .main-word {
        font-weight: bold;
        font-size: 7vw;
        position: static;
        letter-spacing: 10px;
        margin-top: 20px;
    }
}
.hangman, .platform {
  position: relative;
  margin-top: 25px;
}

.hangman div, .platform div {
  position: absolute;
}

.head {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 5px solid black;
  top: 86px;
  right: 143px;
}

.body {
  width: 5px;
  height: 75px;
  border: 3px solid black;
  top: 135px;
  right: 165px;
}

.left-arm {
  width: 5px;
  height: 37.5px;
  border: 3px solid black;
  top: 140px;
  right: 178px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.right-arm {
  width: 5px;
  height: 37.5px;
  border: 3px solid black;
  top: 140px;
  right: 152px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.left-leg {
  width: 5px;
  height: 37.5px;
  border: 3px solid black;
  top: 205px;
  right: 173px;
  -webkit-transform: rotate(25deg);
          transform: rotate(25deg);
}

.right-leg {
  width: 5px;
  height: 37.5px;
  border: 3px solid black;
  top: 205px;
  right: 157px;
  -webkit-transform: rotate(-25deg);
          transform: rotate(-25deg);
}

.left-eye {
  width: 5px;
  height: 37.5px;
  top: 95px;
  right: 176px;
  font-weight: bold;
}

.right-eye {
  width: 5px;
  height: 37.5px;
  top: 95px;
  right: 160px;
  font-weight: bold;
}

.base {
  width: 200px;
  height: 50px;
  border: 5px solid black;
  top: 265px;
  right: 0px;
}

.rope {
  width: 100px;
  height: 75px;
  border: 5px solid black;
  border-bottom: transparent;
  top: 15px;
  right: 70px;
}

.stick {
  width: 10px;
  height: 250px;
  border: 5px solid black;
  top: 15px;
  right: 65px;
}

@media only screen and (max-width:767px) {
  .full-hangman {
    width: 70%;
  }
}


.wrong-letters {
    margin-top: 30px;
}

.letter {
    margin: 4px;
}
.container {
    margin: auto 0 auto 0;
    padding: 0;
    max-width: 100%;
}

.row:not(.navbar-row) {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
body {
  padding-right: 0px !important;
  background-color: lightgray;
}

.App {
  text-align: center;
  background-color: white;
}

footer {
  margin-top: 45px;
  padding: 15px;
  background-color: lightgray;
}
